import { ReplaySubject, of, fromEvent, merge } from "rxjs";
import { filter, map,  switchMap } from "rxjs/operators";
/*
  putState:
  loaderCompleted
  waitCacheList//loaderCompleted
  statekey
 */
export default () => {
  let putState = new ReplaySubject(1);
  const waitCacheList = putState.pipe(
    filter(({ type }) => type === "getCacheList")
  );
  const distance$ = fromEvent(document, "touchend").pipe(
    map(() => document.documentElement.scrollTop)
  );
  const outState = waitCacheList.pipe(
    switchMap(({ element, state }) => {
      const targetDistance =
        element.getBoundingClientRect().top - window.innerHeight; //
      const copyState = JSON.parse(JSON.stringify(state));
      const repetitionCache$ = of(targetDistance).pipe(
        filter(value => value < window.innerHeight)
      );
      const loadingCompleted = distance$.pipe(
        filter(distance => distance > targetDistance)
      );
      return merge(repetitionCache$, loadingCompleted).pipe(
        map(() => Object.assign(copyState, { wait: false }))
      );
    })
  );
  return { putState, outState };
};



